.AvaImg img,
.AvaImgGuest img {
  height: 200px;
  width: 200px;
  border-radius: 4px;
  box-shadow: 0 0 5px 0px black;
}

.profileInfo {
  display: grid;
  grid-template-columns: 2fr 10fr;
  gap: 15px;
  background-color: #ebe8e8;
}

.AvaImg {
  height: 240px;
  box-shadow: 0px 0px 0px 0px black;
}

.AvaImgGuest {
  height: 200px;
}

.profileForm {
  background-color: rgb(255, 255, 255, 0.8);
  border-radius: 4px;
  padding: 10px;
  box-shadow: 0px 0px 0px 2px black;
}

.userInfo {
  background-color: rgb(255, 255, 255, 0.8);
  border-radius: 4px;
  padding: 10px;
  box-shadow: 0px 0px 0px 2px black;
}

.userInfo div {
  padding: 5px;
  padding-top: 0px;
}

.userInfo .item {
  color: #4084af;
}

.statusitem {
  width: 460px;
  height: 50px;
  display: inline-block;
  word-wrap: break-word;
}

.userName {
  font-size: 25px;
  font-weight: 5px;
  font-weight: bold;
  display: inline;
}

.contacts {
  margin-left: 30px;
}

.contactsHead {
  margin-top: 32px;
  background-color: rgb(241, 241, 241);
  cursor: pointer;
  font-weight: bold;
}

.contactsHead:hover {
  background-color: rgb(224, 224, 224);
}

.contactsHead img {
  width: 13px;
  margin-left: 590px;
  margin-top: 10px;
}

.contactsHead span {
  font-size: 25px;
}

.addPhoto label {
  border-radius: 4px;
  box-shadow: 0 0 5px 0px black;
  cursor: pointer;
  background-color: rgb(255, 84, 53);
  color: white;
  display: inline-block;
  margin-top: 5px;
  padding: 3px;
  width: 193px;
  text-align: center;
  font-size: 17px;
  font-weight: bold;
  font-size: large;
}

.addPhoto button {
  background-color: #00a2dd;
  border-radius: 4px;
  box-shadow: 0 0 5px 0px black;
  cursor: pointer;
  color: white;
  display: inline-block;
  margin-top: 5px;
  padding: 4px 3px 4px 3px;
  width: 200px;
  text-align: center;
  font-size: 17px;
  font-weight: bold;
  font-size: large;
}

.addPhoto label:hover {
  background-color: #00a2dd;
}

.addPhoto button:hover {
  background-color: rgb(255, 84, 53);
}

.AvaImg input {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  z-index: -1;
}

.editButton {
  font-weight: bold;
  color: rgb(255, 38, 0);
  cursor: pointer;
  margin-right: 5px;
  margin-top: 10px;
  float: right;
}

.menu {
  display: flex;
  margin-top: 20px;
}

.menu img {
  width: 25px;
  height: 25px;
  background-color: rgb(151, 185, 183, 0.5);
  padding: 7px;
  border-radius: 35%;
}

.menu .items {
  padding: 5px 5px 1px 5px;
  background-color: rgb(185, 45, 45);
  border-radius: 20px;
  margin-left: 14px;
}

.menu img:hover {
  background-color: rgb(151, 185, 183, 0.7);
  box-shadow: 0px 0px 2px 0px black;
}

.editButton:hover {
  color: rgb(211, 32, 0);
}

.formInputs {
  margin-bottom: 10px;
}

.profileForm .item {
  font-weight: bold;
  color: rgb(204, 99, 81);
}

.profileForm label {
  font-weight: bold;
  float: right;
  color: rgb(255, 38, 0);
  cursor: pointer;
}

.profileForm label:hover {
  color: rgb(255, 38, 0);
}

.unfollow button {
  background-color: red;
  color: white;
}
