.PostImg img {
  height: 110px;
  width: 110px;
  background-color: white;
  padding: 7px;
  border-radius: 70px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.PostImg {
  text-align: center;
  background-color: rgba(221, 221, 221, 0.7);
}

.PostImg button {
  padding-top: 10px;
}

.likesWord {
  font-weight: bold;
  opacity: 0.5;
}

.likesZone {
  display: flex;
  text-align: center;
  margin-left: 10px;
  margin-top: 15px;
}

.likes {
  background-color: red;
  padding: 2px 5px 2px 5px;
  border-radius: 4px;
  margin-left: 20px;
  color: white;
  opacity: 0.8;
}

.Posts {
  margin-left: 30px;
  margin-top: 30px;
  box-shadow: 0px 0px 3px 0 black;
  padding: 15px;
  width: 660px;
  background-color: white;
  border-radius: 4px;
  display: grid;
  grid-template-columns: 3fr 10fr;
}

.postMessage {
  display: inline-block;
  margin-top: 0;
  margin-left: 20px;
  word-break: break-all;
}

.Posts button {
  padding: 5px;
  margin-top: 10px;
  width: 100%;
}
