.MyPosts {
  background-color: rgb(255, 255, 255, 0.7);
  border-radius: 4px;
  padding: 10px;
  padding-top: 0px;
  box-shadow: 0px 0px 4px 0px black;
  float: right;
  width: 753px;
}

.MyPostsForm {
  float: right;
  margin-bottom: 15px;
}

.userPosts {
  display: inline-block;
  background-color: tomato;
  padding: 0 10px 0 10px;
  border-radius: 4px;
  color: white;
}

.moiPosts {
  display: inline-block;
  background-color: rgb(104, 104, 255);
  padding: 0 10px 0 10px;
  border-radius: 4px;
  color: white;
}

.MyPosts .Publickbutton {
  padding: 5px;
}

.textareaButton {
  padding: 15px;
  background-color: white;
  border-radius: 4px;
  float: right;
  /* background-color: rgb(221, 239, 255); */
  margin-bottom: 20px;
  box-shadow: 0 0 1px 1px black;
}

h2 {
  text-align: center;
}

.textareaButtonBlock {
  display: grid;
  grid-template-columns: 2fr 10fr;
  padding: 15px;
  border-radius: 4px;
  width: 733px;
}

.textareaButton textarea {
  float: left;
  border: none;
  background-color: rgb(221, 239, 255);
}

.Post {
  padding-bottom: 50px;
}

.buttonForm {
  margin-left: 10px;
  margin-top: 5px;
}

.textareaButtonBlock img {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  display: inline-block;
}
