.Nav {
  grid-area: n;
  height: 130px;
  margin-bottom: 20px;
}

.item {
  font-size: 22px;
  padding-left: 5px;
  letter-spacing: 1px;
  display: inline-block;
  margin-left: 5px;
  font-size: 24px;
}

.activeLink {
  font-weight: bold;
  color: rgb(255, 38, 0);
  padding: 5px 5px 5px 0px;
  border-radius: 6px 0px 0px 6px;
}

.item:hover {
  color: tomato;
}

.Nav img {
  width: 17px;
  margin-left: 7px;
}

.items {
  margin-top: 5px;
}
