.users img {
  width: 75px;
  height: 75px;
  background-color: white;
  border-radius: 35%;
  box-shadow: 0px 0px 0px 0px black;
  padding: 3px;
}

.users {
  padding: 6px;
  display: inline-block;
  width: 130px;
  text-align: center;
  margin-left: 45px;
  margin-bottom: 20px;
  margin-top: 20px;
  box-shadow: 0 0 2px 0.2px black;
  word-wrap: break-word;
  border-radius: 8px;
  height: 150px;
  overflow: hidden;
  background-color: white;
}

.users:hover img {
  width: 78px;
  height: 78px;
}

.users:hover {
  box-shadow: 0 0 3px 0.5px black;
}

.users .name {
  font-size: 15px;
  overflow: hidden;
  word-wrap: break-word;
  display: inline-block;
}

.unfollowColor {
  background-color: tomato;
  color: white;
}

.unfollowColor:hover {
  background-color: rgb(255, 58, 23);
}

.followColor {
  background-color: rgb(96, 132, 185);
}

.usersBlock {
  background-color: rgb(255, 255, 255, 0.7);
  box-shadow: 0px 0px 0px 2px black;
  border-radius: 4px;
}
